$primary: #333;
$color: white;

@font-face {
  font-family: DrukWide;
  src: url(../fonts/DrukWide-Medium.ttf);
}
@keyframes fadeAppear {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}
@keyframes toBlur {
  from {
    filter: blur(0px) saturate(100%);
    -webkit-filter: blur(0px) saturate(100%);
  }
  to {
    filter: blur(20px) saturate(180%);
    -webkit-filter: blur(20px) saturate(180%);
  }
}
@keyframes griditemAppear {
  from {
    opacity: 0.5;
    transform: translateY(20%);
    -webkit-transform: translateY(20%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
    -webkit-transform: translateY(0%);
  }
}

.fadeAnimate {
  animation: griditemAppear 1s ease-in-out forwards;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  color: $color;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  font-weight: 400;
}
.container {
  padding-inline: 10%;
}
h1,
h2,
h3 {
  font-weight: 700;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
}

header {
  background-color: rgba(68, 68, 68, 0.8);
  backdrop-filter: saturate(180%) blur(20px);
  -webkit-backdrop-filter: saturate(180%) blur(20px);
  padding-block: 2rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0 0 2rem;
  position: fixed;
  width: 100%;
  z-index: 9999;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      height: 2rem;
      width: auto;
    }
    nav {
      display: flex;
      justify-content: space-around;
      align-items: center;
      ul {
        display: flex;
        align-items: center;
        gap: 2rem;
        font-size: 1rem;
      }
    }
    .button {
      border: white 2px solid;
      padding: 0.5rem 1rem;
      border-radius: 9999rem;
      font-size: 1rem;
    }
  }
}
main {
  padding-top: 2rem;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10%;
  position: relative;
  background-color: $primary;
  .background-img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    animation: toBlur ease-in-out 2s forwards;
    background-image: url(../assets/main.jpeg);
    background-size: cover;
    background-position: center;
  }
  .left {
    z-index: 1;
    width: 60%;
    h1 {
      text-transform: uppercase;
      font-weight: 700;
      font-size: 4vw;
      text-align: justify;
      margin-bottom: 1rem;
      font-family: "DrukWide";
      line-height: 120%;
      opacity: 0%;
      animation: fadeAppear 500ms 1s ease-in-out forwards;
    }
    p {
      width: 100%;
      filter: opacity(50%);
      -webkit-filter: opacity(50%);
      opacity: 0%;
      animation: fadeAppear 500ms 1150ms ease-in-out forwards;
      font-size: 1rem;
      height: fit-content;
      margin-bottom: 2rem;
    }
    .button {
      display: none;
      width: fit-content;
      margin: auto;
      height: 0px;
      border: white 2px solid;
      padding: 0.5rem 1rem;
      border-radius: 9999rem;
      font-size: 1rem;
      opacity: 0%;
      animation: fadeAppear 500ms 1150ms ease-in-out forwards;
    }
  }
  .right {
    z-index: 1;
    width: 40%;
    opacity: 0%;
    animation: fadeAppear 500ms 1300ms ease-in-out forwards;
    padding: 2rem;
    background-color: rgba(68, 68, 68, 0.8);
    border-radius: 1rem;
    backdrop-filter: saturate(180%) blur(20px);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    h2 {
      opacity: 0%;
      animation: fadeAppear 500ms 1450ms ease-in-out forwards;
      font-size: 2rem;
    }
    a {
      width: 100%;
      img {
        width: 100%;
        height: auto;
        border-radius: 0.5rem;
        object-fit: cover;
        object-position: center;
        opacity: 0%;
        animation: fadeAppear 500ms 1600ms ease-in-out forwards;
      }
    }
    span {
      filter: opacity(50%);
      -webkit-filter: opacity(50%);
      text-align: center;
      opacity: 0%;
      animation: fadeAppear 500ms 1750ms ease-in-out forwards;
      font-size: 1rem;
    }
  }
  .main-scroll {
    position: absolute;
    bottom: 12%;
    left: 50%;
    font-size: 1.5rem;
    transform: translate(-50%);
    animation: blink ease-in-out 2s infinite;
  }
  @keyframes blink {
    0% {
      opacity: 50%;
    }
    50% {
      opacity: 0%;
    }
    100% {
      opacity: 50%;
    }
  }
}
.works {
  padding-block: 2rem;
  * {
    color: $primary;
  }
  h2 {
    font-size: 3rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    i {
      font-size: 2rem;
    }
  }
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
    gap: 2rem;
    width: 100%;
    padding-top: 1rem;
    grid-auto-flow: dense;
    .grid-item {
      background-color: $primary;
      padding: 2rem;
      border-radius: 1rem;
      opacity: 0.5;
      transform: translateY(20%);
      -webkit-transform: translateY(20%);
      display: flex;
      flex-direction: column;

      * {
        color: white;
      }
      .content {
        display: flex;
        height: 100%;
        flex-direction: column;
        justify-content: space-between;
      }
      p {
        padding-top: 1rem;
        opacity: 50%;
      }
      a {
        width: fit-content;
        margin-top: 1rem;
        padding: 0.5rem 1rem;
        border: 2px white solid;
        border-radius: 9999;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        font-size: 0.7rem;
        i {
          font-size: 1rem;
        }
      }
    }
  }
}
.contact {
  padding-block: 2rem;
  * {
    color: $primary;
  }
  h2 {
    font-size: 3rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    i {
      font-size: 2rem;
    }
  }
  .contact-block {
    width: 100%;
    height: 70vh;
    display: flex;
    overflow: hidden;
    border-radius: 1rem;
    margin-top: 1rem;
    .left {
      width: 50%;
      background-image: url(../assets/contact.jpeg);
      background-size: cover;
      background-position: center;
    }
    .right {
      width: 50%;
      display: flex;
      gap: 0.5rem;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      background-color: $primary;
      * {
        color: white;
      }
      h3 {
        font-size: 2rem;
        margin-bottom: 1rem;
      }
      div {
        display: flex;
        flex-direction: column;
        width: fit-content;
        span {
          display: flex;
          gap: 1rem;
          align-items: center;
          line-height: 180%;
        }
        .contact-mail {
          margin-top: 1rem;
          width: 100%;
          margin-top: 1rem;
          padding: 0.5rem 1rem;
          border: 2px white solid;
          border-radius: 9999;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 0.5rem;
          font-size: 1rem;
          i {
            font-size: 1rem;
          }
        }
      }
    }
  }
}
footer {
  background-color: $primary;
  padding-block: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  #copyright {
    text-align: center;
    opacity: 50%;
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 800px) {
  .container {
    padding-inline: 5%;
  }
  header {
    padding-block: 1.5rem;
    nav {
      display: none;
      ul {
        gap: 1rem !important;
      }
    }
    .button {
      display: none;
    }
  }
  main {
    .left {
      width: 100%;
      h1 {
        font-size: 9vw;
        line-height: 140%;
      }
      .button {
        display: block;
        height: fit-content;
      }
    }
    .right {
      width: 0%;
      display: none;
    }
  }
  .contact-block {
    position: relative;
    .left {
      position: absolute !important;
      width: 100% !important;
      height: 100% !important;
    }
    .right {
      width: 100% !important;
      background-color: rgba(68, 68, 68, 0.5) !important;
      backdrop-filter: saturate(180%) blur(20px) !important;
      -webkit-backdrop-filter: saturate(180%) blur(20px) !important;
      z-index: 1;
      border-radius: 1rem;
    }
  }
}

@media (hover: hover) {
  .button {
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    * {
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
      -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    }
  }
  .button:hover {
    background-color: white;
    color: $primary !important;
    * {
      color: $primary !important;
    }
  }
  header {
    nav ul {
      li {
        position: relative;
      }
      li::before {
        content: "";
        position: absolute;
        bottom: -4px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: white;
        border-radius: 9999rem;
        transform: scaleX(0%);
        -webkit-transform: scaleX(0%);
        opacity: 0%;
        transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
        -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
        will-change: transform;
      }
      li:hover::before {
        transform: scaleX(100%);
        -webkit-transform: scaleX(100%);
        opacity: 100%;
      }
    }
  }
  main {
    .right {
      a {
        transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
        -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
      }
      a:hover {
        transform: scale(110%);
      }
    }
  }
}
